.whitePaper {
  // position: relative;
  ol,
  ul,
  dl {
    margin-bottom: 0 !important;
  }
  p {
    margin-bottom: 0 !important;
  }
  height: 100%;
  display: flex;
  flex-direction: column;

  .header {
    width: 100%;
    height: 100px;
    // position: absolute;
    // z-index: 999;
    .head {
      width: 1400px;
      height: 100%;
      margin: 0 auto;
      display: flex;
      align-items: center;
      .logo {
        a {
          display: flex;
        }
        img {
          //   margin-bottom: 0.12rem;
          width: 172px;
          // height: 55px;
        }
      }
      .navList {
        display: flex;
        flex: 1;
        height: 100%;
        align-items: center;
        margin-right: 100px;
        margin-left: 50px;
        li {
          font-size: 16px;
          font-weight: 500;
          color: #4a189f;
          margin-left: 55px;
          cursor: pointer;
          white-space: nowrap;
          position: relative;
          a{
            color:#4A189F;
          }
        }
        li:hover {
          border-bottom: 1px solid #4a189f;
        }
        .active {
          color: #4a189f;
        }
      }
      .descr {
        height: 100%;
        display: flex;
        align-items: center;
        white-space: nowrap;
        a {
          color: rgba(80, 27, 162, 1);
          margin-left: 20px;
          cursor: pointer;
          font-weight: 400;
          font-size: 14px;
        }
        span {
          color: #fff;
          margin-right: 9px;
          cursor: pointer;
        }
        span:nth-child(1) {
          display: inline-block;
          background: rgba(80, 27, 162, 1);
          border-radius: 3rem;
          //   line-height: 0.25rem;
          text-align: center;
          font-size: 16px;
          font-weight: 400;
          padding: 10px 21px;
          box-sizing: border-box;
        }
        span:hover {
          background: #fff;
          color: #501ba2;
          box-shadow: 3px 3px 8px rgba(97, 49, 171, 0.2),
            -3px -3px 8px rgba(97, 49, 171, 0.2);
        }
      }
    }
  }
  section {
    width: 1400px;
    flex: 1;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    position: relative;
    .headTalk {
      width: 100%;
      height: 543px;
      margin: 0 auto;
      position: relative;
      margin-top: 50px;
      .content {
        width: 1232px;
        height: 502px;
        position: absolute;
        z-index: 8;
        right: 0;
        bottom: -1px;
        background: #fff;
        display: flex;
        flex-direction: column;
        h3 {
          font-size: 38px;
          font-weight: 500;
          color: rgba(58, 46, 70, 1);
          line-height: 38px;
          margin-left: 53px;
          margin-top: 59px;
          margin-bottom: 24px;
        }
        >p {
          width: 1129px;
          margin-left: 53px;
          font-size: 24px;
          font-weight: 300;
          color: rgba(93, 86, 102, 1);
          line-height: 32px;
        }
        .down{
          width: 100%;
          height: 50px;
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-top: 35px;
          a{
            display: flex;
            align-items: center;
            label{
              font-size:32px;
              font-weight:400;
              color:rgba(58,46,70,1);
              // line-height:32px;
              margin-right: 30px;
            }
            i{
              width: 103px;
              height: 103px;
              background: url('/img/@2x.png') no-repeat;
              background-size: cover;
            }
          }
          a:hover i{
            background: url('/img/ 2@2x.png') no-repeat;
            background-size: cover;
          }
        }
        span {
          border-left: 1px solid rgba(96, 66, 158, 1);
          font-size: 20px;
          font-weight: 300;
          color: rgba(93, 86, 102, 1);
          line-height: 19px;
          padding-left: 17px;
          box-sizing: border-box;
          margin-left: 53px;
          // margin-top: 40px;
          white-space: nowrap;
        }
      }
      .bg1 {
        width: 1056px;
        height: 543px;
        position: absolute;
        top: 0;
        right: 50px;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .bg2 {
        width: 1104px;
        height: 146px;
        position: absolute;
        bottom: 0;
        left: 50px;
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
    .wrapper {
      width: 100%;
      display: flex;
      flex: 1;
      .wrapHead {
        width: 15%;
        // margin-top: 200px;
        padding-top: 200px;
        .ant-anchor-ink {
          display: none;
        }
        .ant-anchor-link {
          a {
            line-height: 44px;
            font-size: 16px;
            color: rgba(93, 86, 102, 1);
            font-weight: 500;
          }
          a:hover {
            color: rgba(96, 66, 158, 1);
          }
        }
        .ant-anchor-link-active > .ant-anchor-link-title {
          color: rgba(96, 66, 158, 1);
        }
      }
      .post {
        position: static;
      }
      .pos {
        position: fixed;
        top: -100px;
      }
      .local {
        width: 80%;
        // height: 900px;
        overflow: hidden;
        // position: static;
        .introduction {
          h4 {
            font-size: 20px;
            font-weight: 400;
            color: rgba(93, 86, 102, 1);
            line-height: 32px;
            margin-top: 100px;
          }
          h2 {
            font-size: 40px;
            font-weight: 500;
            color: rgba(93, 86, 102, 1);
            // line-height:32px;
            margin-bottom: 70px;
            margin-top: 30px;
          }
          h3 {
            font-size: 24px;
            font-weight: 500;
            color: rgba(93, 86, 102, 1);
            line-height: 32px;
            margin: 30px 0;
          }
          p {
            font-size: 24px;
            color: rgba(93, 86, 102, 1);
            font-size: 300;
            line-height: 32px;
          }
        }
      }
      .poss {
        // height: 937px;
        margin-left: 200px;
        overflow: hidden;
      }
    }
  }
  footer {
    width: 100%;
    margin-top: 200px;

    .footerNav {
      width: 1400px;
      margin: 0 auto;
      padding-left: 50px;
      box-sizing: border-box;
      display: flex;
      margin-bottom: 150px;
      .logo {
        // width: 55px;
        height: 55px;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .nav {
        display: flex;

        div {
          margin-left: 240px;
          display: flex;
          flex-direction: column;
          a {
            font-size: 28px;
            font-weight: 300;
            color: rgba(80, 27, 162, 1);
            line-height: 28px;
            margin-bottom: 49px;
          }
        }
      }
    }
    .email {
      width: 1400px;
      margin: 0 auto;
      padding-left: 50px;
      box-sizing: border-box;
      display: flex;
      justify-content: space-between;
      margin-bottom: 60px;
      p {
        font-size: 14px;
        font-weight: 400;
        color: rgba(56, 47, 68, 1);
        line-height: 14px;
        display: flex;
        img {
          width: 19px;
          height: 15px;
          margin-right: 10px;
        }
        span {
          border-right: 1px solid rgba(56, 47, 68, 0.9);
          padding: 0 28px;
          box-sizing: border-box;
          color: rgba(56, 47, 68, 0.9);
          font-size: 14px;
          line-height: 16px;
        }
        span:last-child {
          border-right: 0;
        }
      }
    }
  }
}
