.developers {
  height: 100%;
  display: flex;
  flex-direction: column;

  .header {
    width: 100%;
    height: 100px;

    // position: absolute;
    // z-index: 999;
    .head {
      width: 1400px;
      height: 100%;
      margin: 0 auto;
      display: flex;
      align-items: center;

      .logo {
        a {
          display: flex;
        }

        img {
          //   margin-bottom: 0.12px;
          width: 172px;
          // height: 55px;
        }
      }

      .navList {
        display: flex;
        flex: 1;
        height: 100%;
        align-items: center;
        margin-right: 100px;
        margin-left: 50px;

        li {
          font-size: 16px;
          font-weight: 500;
          color: #4A189F;
          margin-left: 55px;
          cursor: pointer;
          white-space: nowrap;
          position: relative;

          a {
            color: #4A189F;
          }
        }

        li:hover {
          border-bottom: 1px solid #4A189F;
        }

        .active {
          color: #4A189F;
        }
      }

      .descr {
        height: 100%;
        display: flex;
        align-items: center;
        white-space: nowrap;

        a {
          color: rgba(80, 27, 162, 1);
          margin-left: 20px;
          cursor: pointer;
          font-weight: 400;
          font-size: 14px;
        }

        span {
          color: #FFF;
          margin-right: 9px;
          cursor: pointer;
        }

        span:nth-child(1) {
          display: inline-block;
          background: rgba(80, 27, 162, 1);
          border-radius: 20px;
          //   line-height: 0.25px;
          text-align: center;
          font-size: 16px;
          font-weight: 400;
          padding: 10px 21px;
          box-sizing: border-box;
        }

        span:hover {
          background: #FFF;
          color: #501BA2;
          box-shadow: 3px 3px 8px rgba(97, 49, 171, .2), -3px -3px 8px rgba(97, 49, 171, .2);

        }
      }
    }
  }

  section {
    width: 100%;
    height: auto;

    .vision .head {
      width: 920px;
      height: 61px;
      margin: 140px auto;
      position: relative;
      display: flex;
      align-items: center;
    }

    .vision1 .head {
      width: 930px;
    }

    .vision2 .head {
      width: 870px;
    }

    .vision .head .bg {
      /* width: 1200px; */
      height: 61px;
      margin: 0 auto;
    }

    .vision .head .bg img {
      position: absolute;
    }

    .vision .head .bg i {
      position: absolute;
      top: 50px;
      width: 73px;
      height: 4px;
      left: 50%;
      margin-left: -45px;
      display: flex;
    }

    .vision .head .bg img {
      width: 100%;
      height: 100%;
    }

    .vision .head p {
      font-size: 32px;
      color: rgba(56, 47, 68, .79);
      font-weight: 400;
    }

    .visionContent,
    .vision2Content {
      width: 1200px;
      margin: 0 auto;
      display: flex;
      flex-direction: column;
    }

    .vision2Content .code {
      width: 100%;
      height: 370px;
      display: flex;
    }

    .vision2Content .code img {
      width: 100%;
      height: 100%;
    }

    .vision2Content a {
      /* width: 2.5px; */
      font-size: 30px;
      font-weight: 400;
      margin-top: 10px;
      color: rgba(56, 47, 68, .79);
      margin-left: 920px;
      display: flex;
      align-items: center;
    }

    .vision2Content a i {
      width: 20px;
      height: 25px;
      display: flex;
      margin-right: 15px;
      background: url('/img/xiazai 2@2x.png') no-repeat;
      background-size: 100% 100%;
    }

    .vision2Content a:hover {
      color: #20BBBA;
    }

    .vision2Content a:hover i {
      background: url('/img/xiazai@2x.png') no-repeat;
      background-size: 100% 100%;
    }

    .violasPayDescr {
      width: 100%;
      display: flex;
    }

    .violasPayDescr p {
      width: 24%;
      height: 620px;
      margin-right: 8px;
      display: flex;
    }

    .violasPayDescr p:last-child {
      margin-right: 0;
    }

    .violasPayDescr p img {
      width: 100%;
      height: 100%;
    }

    .downloadPay {
      display: flex;
      margin-top: 150px;
    }

    .downloadPay dl {
      width: 300px;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-left: 200px;
    }

    .downloadPay dl dt {
      width: 300px;
      height: 300px;
      display: flex;
      position: relative;
      border: 1px solid rgba(129, 130, 134, 1);
    }

    .downloadPay dl:nth-child(2) dt {
    }

    .downloadPay dl p {
      font-size: 24px;
      font-weight: 400;
      color: rgba(129, 130, 134, 1);
      position: absolute;
      top: 50%;
      margin-top: -20px;
      left: 50%;
      margin-left: -75px;
      letter-spacing: 2px;
    }

    .downloadPay dl dt img {
      width: 100%;
      height: 100%;
    }

    .downloadPay dl dd {
      width: 221px;
      height: 55px;
      display: flex;
      align-items: center;
      margin-top: 30px;
    }

    .downloadPay dl dd img {
      width: 100%;
      height: 100%;
      border-radius: 28px;
    }
  }

  footer {
    width: 100%;
    margin-top: 200px;

    .footerNav {
      width: 1400px;
      margin: 0 auto;
      padding-left: 50px;
      box-sizing: border-box;
      display: flex;
      margin-bottom: 150px;

      .logo {
        // width: 55px;
        height: 55px;

        img {
          width: 100%;
          height: 100%;
        }
      }

      .nav {
        display: flex;

        div {
          margin-left: 240px;
          display: flex;
          flex-direction: column;

          a {
            font-size: 28px;
            font-weight: 300;
            color: rgba(80, 27, 162, 1);
            line-height: 28px;
            margin-bottom: 49px;
          }
        }
      }
    }

    .email {
      width: 1400px;
      margin: 0 auto;
      padding-left: 50px;
      box-sizing: border-box;
      display: flex;
      justify-content: space-between;
      margin-bottom: 60px;

      p {
        font-size: 14px;
        font-weight: 400;
        color: rgba(56, 47, 68, 1);
        line-height: 14px;
        display: flex;

        img {
          width: 19px;
          height: 15px;
          margin-right: 10px;
        }

        span {
          border-right: 1px solid rgba(56, 47, 68, .9);
          padding: 0 28px;
          box-sizing: border-box;
          color: rgba(56, 47, 68, .9);
          font-size: 14px;
          line-height: 16px;
        }

        span:last-child {
          border-right: 0;
        }
      }
    }
  }
}

